import { Helmet } from "react-helmet-async"
import { useDeviceInfo } from "@rp/react/web/common/hooks"

type Props = {
  title: string
}

const HtmlPageHead = ({ title }: Props) => {
  const { isPWA } = useDeviceInfo()
  const noPwaTitle = title ? `${title} | Bigdata` : "Bigdata"
  return (
    <Helmet>
      <title>{isPWA ? title || " " : noPwaTitle}</title>
    </Helmet>
  )
}

export { HtmlPageHead }
